
.introduction {
  position: absolute;
  width: 100%;
  top: 158px;
  left: 0px;
  background-image: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1495108989503&di=5a2098137d19c56dc7da521c0fe56f2b&imgtype=0&src=http%3A%2F%2Fimg01.taopic.com%2F160605%2F240386-1606050T05641.jpg");
  z-index: -1;
  transition: all 0.5s linear;
}
.introduction ul {
  height: 100%;
  width: 100%;
}
.introduction ul li {
  height: 25%;
  width: 100%;
}
.introduction ul li span {
  display: inline-block;
  width: 35%;
  height: 30px;
  margin-right: 5%;
  line-height: 30px;
  vertical-align: top;
  text-align: right;
  font-size: 18px;
  border-right: 5px solid #000;
  border-bottom: 1px solid #000;
  box-sizing: border-box;
}
.introduction ul li:last-child {
  height: 50%;
}


