
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  font-size: 0;
  position: relative;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .content {
  display: inline-block;
  margin-left: 16px;
  font-size: 14px;
}
.header .content-wrapper .content .title {
  margin: 18px 0 8px 0;
}
.header .content-wrapper .content .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .content .description {
  margin-bottom: 10px;
  margin-top: 20px;
  margin-left: 6px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .avatar_detail {
  position: absolute;
  right: 12px;
  bottom: 14px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .avatar_detail span {
  font-size: 10px;
  vertical-align: top;
}
.header .content-wrapper .avatar_detail span dot {
  display: inline-block;
  height: 1em;
  line-height: 1em;
  text-align: left;
  font-size: 10px;
  vertical-align: -0.5em;
  overflow: hidden;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.header .detail.fade-enter-active,
.header .detail.fade-leave-active {
  transition: all 0.5s;
}
.header .detail.fade-enter,
.header .detail.fade-leave-active {
  opacity: 0;
  background: rgba(7,17,27,0);
}
.header .detail .detail-wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail-wrapper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail-wrapper .title {
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 28px auto 24px auto;
}
.header .detail .detail-wrapper .title .line {
  -ms-flex: 1;
      flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.header .detail .detail-wrapper .title .text {
  padding: 0 12px;
  font-weight: 700;
  font-size: 14px;
}
.header .detail .detail-wrapper .avatar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
  width: 80%;
}
.header .detail .detail-wrapper .avatar .avatar-item {
  width: 118px;
  height: 130px;
}
.header .detail .detail-wrapper .avatar .avatar-item img {
  -ms-flex: 1;
      flex: 1;
}
.header .detail .detail-wrapper .about {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-wrapper .about .about-item {
  width: 95%;
  margin: 0 auto;
  height: 30px;
  line-height: 30px;
}
.header .detail .detail-wrapper .about .about-item .content {
  display: inline-block;
  text-align: left;
}
.header .detail .detail-wrapper .about .about-item .type {
  display: inline-block;
  width: 53px;
}
.header .detail .detail-wrapper .about .about-item a {
  color: #fff;
  text-decoration: underline;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
}
dot::before {
  display: block;
  content: '?\A#\A@\A!';
  vertical-align: top;
  white-space: pre-wrap;
  animation: dot 5s infinite step-start both;
}
@keyframes dot {
25% {
    transform: translateY(-3em);
}
50% {
    transform: translateY(-2em);
}
75% {
    transform: translateY(-1em);
}
}


