
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  opacity: 1;
  display: inline-block;
  padding: 6px;
  transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
  transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter-active,
.cartcontrol .cart-decrease.move-leave-active {
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave-active {
  transform: translate3D(24px, 0, 0);
  opacity: 0;
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  padding: 6px;
  color: #00a0dc;
}


