
.fade_in_content {
  width: 100%;
}
.fade_in_content h3 {
  font-weight: bold;
  margin: 10px 0;
  font-size: 18px;
}
.fade_in_content div {
  word-wrap: break-word;
  line-height: 20px;
}
.fade_in_content.fadein-enter-active,
.fade_in_content.fadein-leave-active {
  transition: all 2s ease-in;
  opacity: 1;
}
.fade_in_content.fadein-enter,
.fade_in_content.fadein-leave-active {
  opacity: 0;
}


