
.introduction2 {
  position: absolute;
  width: 100%;
  top: 158px;
  left: 0px;
  z-index: -1;
  transition: all 0.5s linear;
}
.introduction2 .left {
  float: left;
  width: 40%;
}
.introduction2 .right {
  float: right;
  width: 40%;
}
.introduction2 .span {
  width: 60px;
  display: inline-block;
  height: 100%;
  float: right;
  position: relative;
  font-size: 0;
}
.introduction2 .span .title {
  box-sizing: border-box;
  border-top: 5px solid #000;
  border-left: 1px solid #000;
  height: 30%;
  width: 100%;
  display: inline-block;
  font-size: 18px;
  line-height: 60px;
  text-align: center;
}
.introduction2 .span .title.top {
  position: absolute;
  top: 0;
  left: 0;
}
.introduction2 .span .title.bottom {
  position: absolute;
  bottom: 0;
  left: 0;
}
.introduction2 .span .title span {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  width: 100%;
  height: 100%;
}
.introduction2 .span .empty {
  height: 30%;
  width: 100%;
  display: inline-block;
}
