
.fade_content {
  opacity: 1;
  display: inline-block;
  width: 60%;
  height: 100%;
  box-sizing: border-box;
  padding: 0 10px;
  font-family: "宋体";
}
.fade_content .inner {
  transform: translate3D(0, 0, 0);
  transition: all 0.5s ease-in 1.5s;
  font-size: 18px;
  line-height: 30px;
}
.fade_content.fade_in-enter-active,
.fade_content.fade_in-leave-active {
  transition: all 0.5s ease-in 1.5s;
  opacity: 1;
}
.fade_content.fade_in-enter,
.fade_content.fade_in-leave-active {
  opacity: 0;
}
.fade_content.fade_in-enter .inner,
.fade_content.fade_in-leave-active .inner {
  transform: translate3D(100px, 0, 0);
}


